html{
    scroll-behavior: smooth;
}

body{
    min-width: 600px;
}

.perfil{
    display: flex;
    flex-direction: column;
    min-height: 47rem;
    max-height: 77rem;
    margin-top: 66px;
}

@media screen and (max-width: 1051px){
    .perfil{
        margin-bottom: 40px;
    }
}

#habilidades{
    display: flex;
    flex-direction: column; 
    justify-content: space-around;
    align-items: center;
    min-height: 20rem;
    max-height: 55rem;
}

#blog{
    min-height: 45rem;
    max-height: 100rem;
}

.title{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

#habilidades{
    background-image: url("/t2/assets/features-bg.jpg");
    background-size: cover;
    text-align: center;
}

#p{
    color: #fff;
}
.navbar-transparent{
    background-color: transparent !important;
}
.title{
    font-family: sans-serif;
    font-size: 40px;
    margin-top: 15px;
}

.title.black:after{
    content: " ";
    display: block;
    border: 2.5px solid;
    width: 100px;
    margin: 10px;
}

.title.white:after{
    content: " ";
    display: block;
    border: 2.5px solid;
    width: 100px;
    margin: 10px;
}

.white{
    color: white;
}

.black{
    color: black;
}
.foto{
    margin-top: 5px;
    height: 50vh;
    width: 50vh;
    border-radius: 50%;
}

.center{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 25px;
}

.lista{
    margin-top: 30px;
}

#introducao{
    margin-top: 15px;
    text-align: center;
}
#botao{
    text-align: center;
    height: 60px;
    width: 240px;
    background: transparent;
    border: 1px solid black;
    border-radius: 50px;
    -webkit-transition: 2s;
    transition: 2s;
}

#botao:hover{
    cursor: pointer;
    background: black;
    color: white;
}

#botao-white{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    width: 250px;
    background: transparent;
    border: 1px solid white;
    border-radius: 50px;
    -webkit-transition: 2s;
    transition: 2s;
}

#botao-white:hover{
    cursor: pointer;
    background: white;
    color: black;
}

.link{
    font-size: 1rem;
    color: transparent;
}

.link:hover{
    text-decoration: none;
}

.link:focus{
    outline-color: transparent;
    color: transparent;
}
#flex{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.skill{
    padding: 3px 10px;
    background-color: rgba(0,0,0,.4);
    color: #fff;
    margin: 4px;
    cursor: pointer;
    -webkit-transition: all .4s!important;
    transition: all .4s!important;
    font-family: Montserrat,Helvetica,Arial,Lucida,sans-serif;
    text-transform: uppercase;
    min-width: 60px;
    font-size: 10pt;
}

.skill:hover{
    background-color: #fff;
    color: #000;
}
.center{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.flex{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 375px;   
    max-height: 650px;    
    justify-content: center;
    align-items: center;
    background: rgba(80, 80, 80, .4);
    padding: 30px;
    border-radius: 10px;
    margin-top: 10px;
}

#img{
    height: 200px;
    width: 320px;
    margin-bottom: 10px;
    border-radius: 5px;
}

